import { css } from 'lit';

export const styles = css`:host {
  --track-color: var(--ig-linear-bar-track-color, hsla(var(--ig-gray-300), var(--ig-gray-a)));
  --fill-color-default: var(--ig-linear-bar-fill-color-default, hsla(var(--ig-primary-500), var(--ig-primary-a)));
  --fill-color-danger: var(--ig-linear-bar-fill-color-danger, hsla(var(--ig-error-500), var(--ig-error-a)));
  --fill-color-warning: var(--ig-linear-bar-fill-color-warning, hsla(var(--ig-warn-500), var(--ig-warn-a)));
  --fill-color-info: var(--ig-linear-bar-fill-color-info, hsla(var(--ig-info-500), var(--ig-info-a)));
  --fill-color-success: var(--ig-linear-bar-fill-color-success, hsla(var(--ig-success-500), var(--ig-success-a)));
  --stripes-color: var(--ig-linear-bar-stripes-color, hsla(var(--ig-gray-50), 0.45));
  --text-color: var(--ig-linear-bar-text-color, hsla(var(--ig-gray-700), var(--ig-gray-a)));
  --track-border-radius: var(--ig-linear-bar-track-border-radius, clamp(0rem, calc(var(--ig-radius-factor, 0) * 0.5rem), 0.5rem));
}

[part~=track] {
  border-radius: var(--track-border-radius);
}

[part~=striped]:not([part~=indeterminate]) {
  background: hsla(var(--ig-secondary-500), var(--ig-secondary-a)) repeating-linear-gradient(45deg, hsla(var(--ig-secondary-500), var(--ig-secondary-a)), hsla(var(--ig-secondary-500), var(--ig-secondary-a)) 0.375rem, hsla(var(--ig-gray-50), 0.7) 0.375rem, hsla(var(--ig-gray-50), 0.7) 0.875rem);
}`;
